:root{--red:#f3234a;--yellow:#ffd246;--mint:#d7f3d4;--navy:#1c3c5a;--light:#f7f8f8;--smoke:#d9dbdd;--gray:#aab0b5;--blue:#3274d6;--green:#2bb772;--haze:#f7f8f8;--charcoal:#666}body{font-family:-apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Helvetica,Arial,sans-serif;font-size:16px;color:var(--navy);-webkit-text-size-adjust:100%;-ms-text-size-adjust:100%;text-rendering:geometricPrecision;-moz-osx-font-smoothing:grayscale;-webkit-font-smoothing:antialiased}::-moz-selection{background:#ffebad}::selection{background:#ffebad}.container{max-width:100%;width:968px}@media screen and (max-width:968px){.container{padding-left:2em;padding-right:2em}}@media screen and (max-width:525px){.container{padding-left:1.2em;padding-right:1.2em}}.tsm{font-size:.7em;line-height:22px}.tmd{font-size:.85em;line-height:30px}.tlg{font-size:1.25em;line-height:38px}.divider{height:1px;border-color:var(--smoke);border-top:none}.navy{color:var(--navy)}.blue{color:var(--blue)}.bg-haze{background-color:var(--haze)}@media screen and (max-width:525px){.article-nav{display:none}.footer{-ms-flex-direction:column;flex-direction:column;padding-bottom:30px}.footer .copytext{margin-top:10px}}